import React, { useEffect } from 'react'
import { setToHome } from '../../reducers/overlayReducer'
import { connect } from 'react-redux'

const Contact = (props) => {
    useEffect(() => {
        props.setToHome()
    }, [])
    return (
        <h1>contact</h1>
    )
}

export default connect(
    null,
    { setToHome }
)(Contact)