export const setNav = (route) => {
    return async dispatch => {
        dispatch({
            type: 'SET_NAV',
            data: route
        })
    }
}

const navTrackerReducer = (state = 'white', action) => {
    switch (action.type) {
        case 'SET_NAV':
            return action.data
    }
    return state
}

export default navTrackerReducer