import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { setToExpand } from '../../reducers/overlayReducer'

const Slice = (props) => {
    useEffect(() => {
        props.setToExpand()
    }, [])
    
    const bannerStyle = {
        backgroundColor: props.slice.color,
    }

    return (
        <React.Fragment>
            <div style={bannerStyle} className="banner">
                <h1>{props.slice.title}</h1>
            </div>
        </React.Fragment>
    )
}

const mapStateToProps = (state) => {
    return {
        posts: state.posts
    }
}

export default connect(
    mapStateToProps,
    { setToExpand }
)(Slice)