// make sure the community is being tracked with its special features
import React from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { setNav } from '../reducers/navTrackerReducer'

const PieSlice = (props) => {
    const polarToCartesian = (centerX, centerY, radius, angleInDeg) => {
        const angleInRadians = (angleInDeg - 90) * Math.PI / 180

        return {
            x: centerX + (radius * Math.cos(angleInRadians)),
            y: centerY + (radius * Math.sin(angleInRadians))
        }
    }

    const describeArc = (x, y, radius, startAngle, endAngle) => {
        const start = polarToCartesian(x, y, radius, endAngle)
        const end = polarToCartesian(x, y, radius, startAngle)

        const largeArcFlag = endAngle - startAngle <= 180 ? '0' : '1'

        const d = [
            'M', x, y,
            'L', start.x, start.y,
            'A', radius, radius, 0, largeArcFlag, 0, end.x, end.y,
            'Z'
        ].join(' ')

        return d
    }

    const slice1A = describeArc(500, 500, 480, 0 - props.sliceData.percent / 2, props.sliceData.percent / 2)
    
    const rotateDeg = props.sliceData.clock +  props.sliceData.percent / 2
    const rotateAll = `rotate(${rotateDeg}, 500, 500)`
    const rotateText = `rotate(${-rotateDeg}, 500, 250)`

    const textXPos = (500 - ((props.sliceData.title.length / 2) * 23)).toString()
    const sliceId = `slice${props.sliceData._id}`

    const displayText = props.overlay === 'expanded' ?
        'block' : 'none'

    const currentColor = props.slices.find(s => s._id === props.navTracker) ?
        props.slices.find(s => s._id === props.navTracker).color
        :
        'white'
        
    return (
        <Link to={`/slice/${props.sliceData._id}`} onClick={() => props.setNav(props.sliceData._id)} onMouseEnter={() => props.toSetPieHovered(props.sliceData._id)} onMouseLeave={() => props.toSetPieHovered(null)} transform={rotateAll} className="pie-slice-container">
            <clipPath id={sliceId}>
                <path d={slice1A} fill="green" />
            </clipPath>
            <g clipPath={`url(#${sliceId})`}>
                <path d={slice1A} stroke="white" strokeWidth="10" fill={props.sliceData.color}  className="pie-slice-bg"/>
                <text display={displayText} x={textXPos} y="250" fontSize="40" fill="white" fontWeight="bold"  transform={rotateText} >{props.sliceData.title}</text>
            </g>
        </Link>
    )
}
const mapStateToProps = (state) => {
    return {
        overlay: state.overlay,
        slices: state.slices,
        navTracker: state.navTracker,
    }
}

export default connect(
    mapStateToProps,
    { setNav }
)(PieSlice)