import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { setToExpand } from '../../reducers/overlayReducer'
import sliceService from '../../service/sliceService'

const Home = (props) => {
    useEffect(() => {
        props.setToExpand()
    }, [])


    const bannerStyle = {
        backgroundColor: 'white',
        color: '#282828'
    }
        
    return (
        <React.Fragment>
            <div style={bannerStyle} className="banner">
                <h1>Home</h1>
            </div>
            <div>
                <h1>Explore</h1>
            </div>
            <div>
                <h1>Posts</h1>
            </div>
            <div>
                <h1>Extra</h1>
            </div>
        </React.Fragment>
    )
}

const mapStateToProps = (state) => {
    return {
        posts: state.posts,
        slices: state.slices
    }
}

export default connect(
    mapStateToProps,
    { setToExpand }
)(Home)