import React from 'react'
import { connect } from 'react-redux'
import { setToExpand } from '../reducers/overlayReducer'


const PieContract = (props) => {
    const display = props.overlay === 'expanded' ? 'block' : 'none'

    const contractStyle = {
        display
    }

    return (
        <svg style={contractStyle} onClick={() => props.setToExpand()} className="pie-overlay">
            <circle cx="110" cy="110" r="110" fill="white" opacity="0.6" className="pie-overlay-bg" />
            <svg x="45" y="45" height="120" width="120" viewBox="0 0 64 64" xmlns="http://www.w3.org/2000/svg">
                <path transform='rotate(180, 19, 45)' fill="#282828" d="m26.586 34.586-14.586 14.586v-5.172c0-1.104-.896-2-2-2s-2 .896-2 2v10c0 1.104.896 2 2 2h10c1.104 0 2-.896 2-2s-.896-2-2-2h-5.172l14.586-14.586c.781-.781.781-2.047 0-2.828-.78-.781-2.048-.781-2.828 0z"/>
                <path transform='rotate(180, 45, 19)' fill="#282828" d="m54 8h-10c-1.104 0-2 .896-2 2s.896 2 2 2h5.172l-14.586 14.586c-.781.781-.781 2.047 0 2.828.391.391.902.586 1.414.586s1.023-.195 1.414-.586l14.586-14.586v5.172c0 1.104.896 2 2 2s2-.896 2-2v-10c0-1.104-.896-2-2-2z"/>
            </svg>
        </svg>
    )
}

const mapStateToProps = (state) => {
    return {
        overlay: state.overlay,
    }
}

export default connect(
    mapStateToProps,
    { setToExpand }
)(PieContract)