import React, {useEffect} from 'react'
import { initializeSlices } from './reducers/sliceReducer'
import { connect } from 'react-redux'
import {
  BrowserRouter as Router,
  Route
} from 'react-router-dom'
import NavBar from './components/NavBar'
import Home from './components/pages/Home'
import Slice from './components/pages/Slice'
import About from './components/pages/About'
import Policies from './components/pages/Policies'
import Contact from './components/pages/Contact'
import './static/css/base.css'

// add proper slice reducer to implement slices into frontend from backend

const App = (props) => {
  useEffect(() => {
    props.initializeSlices()
  }, [])
  const getSliceById = (id) => {
    return props.slices.find(s => s._id === id)
  }

  return (
    <div className="wrapper">
      <Router>
        <NavBar />
        <Route exact path="/" render={() => <Home /> } />
        <Route exact path="/slice/:id" render={({match}) => <Slice slice={getSliceById(match.params.id)} /> } />
        <Route path="/about" render={() => <About /> } />
        <Route path="/policies" render={() => <Policies /> } />
        <Route path="/contact" render={() => <Contact /> } />
      </Router>
    </div>
  );
}
const mapStateToProps = (state) => {
  return {
    slices: state.slices,
    overlay: state.overlay
  }
}

export default connect(
  mapStateToProps,
  { initializeSlices }
)(App)