import sliceService from '../service/sliceService'


export const initializeSlices = () => {
    return async dispatch => {
        const slices = await sliceService.getAll()
        dispatch({
            type: 'INIT_SLICES',
            data: slices
        })
    }
}

const sliceReducer = (state = [], action) => {
    switch (action.type) {
        case 'INIT_SLICES':
            return action.data
    }
    return state
}

export default sliceReducer