export const setToHome = () => {
    return async dispatch => {
        dispatch({
            type: 'TO_HOME',
            data:  'toHome'
        })
    }
}

export const setToExpand = () => {
    return async dispatch => {
        dispatch({
            type: 'TO_EXPAND',
            data:  'toExpand'
        })
    }
}

export const setExpanded = () => {
    return async dispatch => {
        dispatch({
            type: 'EXPANDED',
            data: 'expanded'
        })
    }
}

export const setUnique = (x) => {
    
}

const overlayReducer = (state = 'toHome', action) => {
    switch (action.type) {
        case 'TO_HOME':
            return action.data
        case 'TO_EXPAND':
            return action.data
        case 'EXPANDED':
            return action.data
    }
    return state
}

export default overlayReducer