import React, { useState } from 'react'
import '../static/css/pieNav.css'
import PieSlice from './PieSlice'
import PieOverlay from './PieOverlay'
import PieContract from './PieContract'
import { connect } from 'react-redux'
import { setExpanded } from '../reducers/overlayReducer'
import '../static/css/pieNav.css'

const PieNav = (props) => {
    const [pieHovered, setPieHovered] = useState(null)
    const currentSlice = props.slices.find(s => s._id === props.navTracker) ?
        props.slices.find(s => s._id === props.navTracker)
        :
        null
    const csParent = currentSlice ? currentSlice._id : null

    const pieHoveredId = (id) => {
        setPieHovered(id)
    }
    const slicesToShow = props.slices.filter(s => s.parentSlice === csParent)
    let dynamicSliceData = slicesToShow
    const totalLikes = slicesToShow.reduce((total, s) => total + s.likes, 0)
    const likesToPercent = (likes, total) => (likes / total) * 100
    

    if (pieHovered) {
        const hoveredPie = props.slices.find(s => s._id === pieHovered)
        const hoveredPiePercent = likesToPercent(hoveredPie.likes, totalLikes)
        const remainingPercent = 100 - hoveredPiePercent
        if (hoveredPiePercent < 25) {
            dynamicSliceData = dynamicSliceData.map(s => {
                if (s._id === pieHovered) {
                    return {...s, percent: 25}
                } else {
                    const newPercent = (((likesToPercent(s.likes, totalLikes) / remainingPercent) * 100) / 2) * 1.5
                    return {...s, percent: newPercent}
                }
            })
        } else {
            dynamicSliceData = dynamicSliceData.map(s => {
                const percent = likesToPercent(s.likes, totalLikes)
                return {...s, percent}
            })
        }
    } else {
        dynamicSliceData = dynamicSliceData.map(s => {
            const percent = likesToPercent(s.likes, totalLikes)
            return {...s, percent}
        })
    }

    const dataAddCoordinates = (data) => {
        let clock = 0
        return data.map(sliceData => {
            const percToDeg = sliceData.percent * 3.6
            const currentClock = clock
            clock += percToDeg
            return {...sliceData, percent: percToDeg, clock: currentClock}
        })
    }
    dynamicSliceData = dataAddCoordinates(dynamicSliceData)
    const slices = () => dynamicSliceData.map(sliceData => {
        return (
            <PieSlice key={sliceData._id} clock={sliceData.clock} toSetPieHovered={pieHoveredId} sliceData={sliceData} />
        )
    })

    const onClickExpand = () => {
        props.setExpanded()
    }
    const pieStyle = 
        props.overlay === 'expanded' ?
        {
            left: '0px',
            top: '0px',
            width: '500px',
            height: '500px',
            transition: 'left 0.5s, top 0.5s, width 0.5s, height 0.5s',
        }
        :
        {
            left: '-50px',
            top: '-50px',
            width: '200px',
            height: '200px',
            transition: 'left 0.5s, top 0.5s, width 0.5s, height 0.5s',
        }
    

    return (
        <svg style={pieStyle} viewBox="-10 -10 1020 1020" className="pie-container">
            <PieContract />
            <circle cx="500" cy="500" r="500" fill="white" />
            {slices()}
            <circle cx="500" cy="500" r="40" fill="white" />
            <PieOverlay onClickExpand={onClickExpand} />
        </svg>
    )
}

const mapStateToProps = (state) => {
    return {
        slices: state.slices,
        overlay: state.overlay,
        navTracker: state.navTracker
    }
}

export default connect(
    mapStateToProps,
    { setExpanded }
)(PieNav)